import { __, _x } from '@wordpress/i18n';
import { useBlockProps, RichText } from '@wordpress/block-editor';
import BlockInspector from './components/InspectorPanels/BlockInspector';

export default function Edit({ attributes, setAttributes }) {

    const blockProps = useBlockProps({
        className: 'govuk-warning-text'
    })

    return (
        <>
            <BlockInspector />
            <div {...blockProps}>
                <span class='govuk-warning-text__icon' aria-hidden='true'>!</span>
                <strong className='govuk-warning-text__text'>
                    <span class='govuk-visually-hidden'>{_x('Warning', 'screen reader prefix','govuk-components')}</span>
                    <RichText
                        value={attributes.content}
                        onChange={(value) => setAttributes({ content: value })}
                        placeholder={__('Add text about your warning here.', 'govuk-components')}
                        allowedFormats={['bold']}
                    />
                </strong>
            </div>
        </>
       
    )
}